.button-horizontal-scroll {
  max-height: 800px;
  min-height: 200px;
  background: transparent;
  border-color: transparent;
  z-index: -99;
}

.row__song {
  object-fit: contain;
  width: 100%;
  min-height: 200px;
  max-height: 800px;
  margin-right: 20px;
  transition: transform 450ms;
  scroll-snap-align: start;
}

.row__song:hover {
  transform: scale(1.08);
}

.row__songs::-webkit-scrollbar {
  display: none;
}


.card {
  object-fit: contain;
  margin: 30px 20px;
  width: 200px;
  min-height: 200px;
  max-height: 800px;
  height: 100%;
  border-radius: 40px;
box-shadow: 5px 5px 30px 7px rgba(0,0,0,0.25), -5px -5px 30px 7px rgba(0,0,0,0.22);
  cursor: pointer;
  transition: 0.4s;
  scroll-snap-align: start;

}

.card .card_image {
  width: inherit;
  height: inherit;
  border-radius: 40px;
}

.card .card_image img {
  width: inherit;
  height: inherit;
  border-radius: 40px;
  object-fit: cover;
}

.card .card_title {
  text-align: center;
  border-radius: 0px 0px 40px 40px;
  font-family: sans-serif;
  font-weight: bold;
  font-size: 15px;
  margin-top: -50px;
  height: 10px;
}

.card:hover {
  transform: scale(0.9, 0.9);
  box-shadow: 5px 5px 30px 15px rgba(0,0,0,0.25), 
    -5px -5px 30px 15px rgba(0,0,0,0.22);
}

.title-white {
  color: white;
}